import fire from "./firebase";
const checkUserExists = async (id) => {
    const newUserData = {
        id: id,
        messages: [],
        status: 'Who\'s that?'
    }
    fire.firestore().collection('users').doc(id.toString()).get().then(
        r => {
            console.log(!r.exists);
            if(!r.exists){
                console.log('dsadsa');
                fire.firestore().collection('users').doc(id.toString()).set(newUserData).then(
                    r => {console.log('Created user...');return false;}
                );
            }
            else{
                console.log('aye');
                return true;
            }
        }
    );
}
export default checkUserExists;