import React from "react";

const Message = (e) => {
    let date = new Date(e.message.time*1000);
    return (
        <div onClick={e.openMessageModal} className={`message ${e.message.sent ? 'sent' : 'received'}`}><span>{e.message.text}<div className="messageDate">{date.getHours()+'.'+date.getMinutes()}</div></span></div>
    )
}

export default Message;